import React, {Component} from "react";
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux';
import ModalWrapper from '../../Components/Modal/ModalWrapper'
import ModalContent from '../../Components/Modal/ModalContent'
import ModalClose from '../../Components/Modal/ModalClose'
import ModalHeader from '../../Components/Modal/ModalHeader'
import ModalFooter from '../../Components/Modal/ModalFooter'
import ModalButton from '../../Components/Modal/ModalButton'
import ModalError from '../../Components/Modal/ModalError'
import ModalConfirm from '../../Components/Modal/ModalConfirm'

export const ContactFr = ({dispatch, modalSAtatus}) => (<ModalWrapper>
  <ModalContent>
    <ModalClose onClick={() => {
        dispatch(({type: "MODAL_HIDE", payload: false}))
      }}>&times;</ModalClose>

    <ModalHeader>
      <h5>J'ai besoin d'un site internet?</h5>
    </ModalHeader>
    <form ref={self => this.contactForm = self}>
      <div className="form-row">
        <div className="col-12 col-md-6">
          <label>Nom</label>
          <input type="text" placeholder="Nom" name="lastname"/>
        </div>
        <div className="col-12 col-md-6">
          <label>Prnom</label>
          <input type="text" placeholder="Prnom" name="firstname"/>
        </div>

        <div className="col-12 col-md-6">
          <label>Tlphone</label>
          <input type="text" placeholder="Tlphone" name="phone"/>
        </div>
        <div className="col-12 col-md-6">
          <label>Langue de prfrence</label>
          <select name="language" defaultValue="English">
            <option>Anglais</option>
            <option>Franais</option>
          </select>
        </div>
        <div className="col-12 col-md-12">
          <label>E-mail</label>
          <input type="email" placeholder="exemple@xxx.com" name="email"/>
        </div>
        <div className="col-12">
          <label>Message</label>
          <textarea rows="4" placeholder="Ecrivez les informations cls de votre project" name="message"></textarea>
        </div>
        {
          modalSAtatus === false && <ModalError className="col-12">
          Dsol... Un problem est survenu lorsque nous avons essay d'envoyer votre email. Verifiez votre connection internet puis ressayez.
            </ModalError>
        }
      </div>

    </form>
    <ModalFooter className="row">
      <div className="col-12 col-md-6" align="center">
        <ModalButton onClick={() => {
            dispatch({type: "MODAL_HIDE", payload: false})
          }}>&times; Fermez</ModalButton>
      </div>
      <div className="col-12 col-md-6" align="center">
        <ModalButton onClick={() => {
            const {
              firstname,
              lastname,
              email,
              phone,
              language,
              message
            } = this.contactForm;

            dispatch({
              type: 'SEND_CONTACT_INFO',
              payload: {
                subject: "I want a website",
                firstname: firstname.value,
                lastname: lastname.value,
                email: email.value,
                phone: phone.value,
                language: language.value,
                message: message.value
              }
            })

          }} primary="primary">Envoyer</ModalButton>
      </div>
    </ModalFooter>
  </ModalContent>

</ModalWrapper>);




export const HireFr = ({dispatch,modalSAtatus}) => (<ModalWrapper>
  <ModalContent>
    <ModalClose onClick={() => {
        dispatch({type: "MODAL_HIDE", payload: false})
      }}>&times;</ModalClose>

    <ModalHeader>
      <h5>J'embauche</h5>
    </ModalHeader>
    <form ref={self => this.contactForm = self}>
      <div className="form-row">
        <div className="col-12 col-md-6">
          <label>Nom</label>
          <input type="text" placeholder="Nom" name="lastname"/>
        </div>
        <div className="col-12 col-md-6">
          <label>Prnom</label>
          <input type="text" placeholder="Prnom" name="firstname"/>
        </div>

        <div className="col-12 col-md-6">
          <label>Tlphone</label>
          <input type="text" placeholder="Tlphone" name="phone"/>
        </div>
        <div className="col-12 col-md-6">
          <label>Langue de prfrence</label>
          <select name="language" defaultValue="English">
            <option>Anglais</option>
            <option>Franais</option>
          </select>
        </div>
        <div className="col-12 col-md-6">
          <label>Entreprise</label>
          <input type="text" placeholder="Entreprise" name="company"/>
        </div>
        <div className="col-12 col-md-6">
          <label>E-mail</label>
          <input type="email" placeholder="exemple@xxx.com" name="email"/>
        </div>
        <div className="col-12">
          <label>Message</label>
          <textarea rows="4" placeholder="Decrivez le poste que vous offrez." name="message"></textarea>
        </div>
        {
          modalSAtatus === false && <ModalError className="col-12">
          Dsol... Un problem est survenu lorsque nous avons essay d'envoyer votre email. Verifiez votre connection internet puis ressayez.
            </ModalError>
        }
      </div>

    </form>
    <ModalFooter className="row">
      <div className="col-12 col-md-6" align="center">
        <ModalButton onClick={() => {
            dispatch({type: "MODAL_HIDE", payload: false})
          }}>&times; Fermer</ModalButton>
      </div>
      <div className="col-12 col-md-6" align="center">
        <ModalButton onClick={() => {
            const {
              firstname,
              lastname,
              email,
              phone,
              language,
              message,
              company
            } = this.contactForm;

            dispatch({
              type: 'SEND_CONTACT_INFO',
              payload: {
                subject: "I want to hire",
                firstname: firstname.value,
                lastname: lastname.value,
                email: email.value,
                phone: phone.value,
                language: language.value,
                message: message.value,
                company: company.value
              }
            })

          }} primary="primary">Envoyer</ModalButton>
      </div>
    </ModalFooter>
  </ModalContent>

</ModalWrapper>);

export const Confirmation = ({dispatch}) => (<ModalWrapper>

  <ModalContent>
    <ModalConfirm>
      Flicitation ! Nous avons envoy votre email avec succs.
      <p>Je vous recontacterai sous 48h.
      </p>
      <p>Merci.</p>
    </ModalConfirm>
    <div className="col-12" align="center">
      <ModalButton onClick={() => {
          dispatch({type: "MODAL_HIDE", payload: false})
        }}>&times; Fermer</ModalButton>
    </div>
  </ModalContent>

</ModalWrapper>);

class ModalFr extends Component {

  render() {
    const {Modal, dispatch, modalSAtatus} = this.props
    switch (Modal) {
      case 'Contact':
        return (<ContactFr dispatch={dispatch} modalSAtatus={modalSAtatus}/>)
      case 'Hire':
        return (<HireFr dispatch={dispatch} modalSAtatus={modalSAtatus}/>)
      case 'Confirmation':
        return (<Confirmation dispatch={dispatch}/>)
      default:
        return (<ContactFr dispatch={dispatch} modalSAtatus={modalSAtatus}/>);
    }
  }
}

const mapStateToProps = (state) => {

  return {Modal: state.UI.Modal, modalSAtatus: state.UI.modalSAtatus}

}
export default withRouter(connect(mapStateToProps)(ModalFr));



